import React from 'react';

import './AboutMe.css';
import border_blue from '../../../img/border_blue.svg';

const AboutMe = () => {

    return (
        <div id="about-me">
            <h1>About Me</h1>
            <h2>Mauricio Garcia de Ceca</h2>
            <h3>Senior Web Developer, Product/Project Manager</h3>
            <p>I have a Computer Science Degree and I've been working in Web Development for more than 10 years (since 2009), mostly in front-end web development.</p>
            <p>I've worked in lots of different of projects over my career for various kinds of companies and businesses, so I know how to handle myself around unplanned situations, and also how to plan for them.</p>
            <p>I have a high skill level in HTML, CSS and Javascript. Currently I'm working mostly with React, and lately I've been getting into the world of Web3/Blockchain (which I love) but I still consider myself a Junior in these areas.</p>
            <p>I'm very curious, I love thinking and being able to solve problems, finding solutions and always learning more! </p>
            <a href="https://maw.dev/cv/CV-MauricioGarciaDeCeca_EN.pdf" title="Maw Resume" rel="noreferrer" target="_blank"><button className="ui button blue" style={{borderImageSource: `url(${border_blue})`}}>Check Out My Resume</button></a>
        </div>
    )
}

export default AboutMe;